<template>
    <div>
        <div v-if="orgInfo.name && scores.READ_ONLY_API_TOKEN" class="my-block">
            <div class="my-centered">
                <b-jumbotron bg-variant="purple" border-variant="dark" text-variant="white" v-if="orgScore >= 50">
                    <template slot="header">Get Your Tee Shirt!</template>
                    <h2>{{orgInfo.name}}<br/>Completed {{orgScore/10}} of 5 Steps</h2>
                </b-jumbotron>
                <b-jumbotron bg-variant="danger" border-variant="dark" text-variant="white" v-if="orgScore < 50">
                    <template slot="header">So Close!</template>
                    <h2>You need to complete all 5 Steps in order to get a tee shirt</h2>
                    <h2>Go back to <a href="https://oktane19.dev">https://oktane19.dev</a> and finish the Steps</h2>
                    <h2>{{orgInfo.name}}<br/>Completed {{orgScore/10}} of 5 Steps</h2>
                </b-jumbotron>
            </div>
        </div>
        <div v-if="orgInfo.name && !scores.READ_ONLY_API_TOKEN" class="my-block">
            <div class="my-centered">
                <b-jumbotron bg-variant="warning" border-variant="dark" text-variant="white">
                    <template slot="header">No Information Found</template>
                    <h2>Org: {{orgInfo.name}}</h2>
                </b-jumbotron>
            </div>
        </div>
        <div v-if="!orgInfo.name" class="my-block">
            <div class="my-centered">
                <img src="../assets/ahahah.gif"/>
            </div>
        </div>
    </div>
</template>

<script>
    import axios from 'axios'

    import appConfig from '../.app.config'
    import orgScoreStore from '../model/orgScoreStore'

    export default {
        name: 'hello',
        data() {
            return {
                name: appConfig.event.name,
                busy: false,
                busyMsg: '',
                scores: orgScoreStore.state.scores,
                orgInfo: orgScoreStore.state.org,
            }
        },
        computed: {
          orgScore: function () {
              if (!this.scores.READ_ONLY_API_TOKEN) { return undefined }
              var score = 0
              Object.values(this.scores).forEach(value => score += value)
              return score
          }
        },
        mounted: function () {
            if (this.$route.params.org) {
                console.log(this.$route.params.org)
                orgScoreStore.setOrgName(this.$route.params.org)
                orgScoreStore.loadScores()
            }
        }
    }

</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">
    .my-block {
        text-align: center;
        height: 100vh;
        white-space: nowrap;
    }

    /* The ghost, nudged to maintain perfect centering */
    .my-block:before {
        content: '\200B';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
    }

    /* The element to be centered, can also be of any width and height */
    .my-centered {
        display: inline-block;
        vertical-align: middle;
    }
</style>
